@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}input[type=number]{-moz-appearance:textfield}

*{font-family:-apple-system, BlinkMacSystemFont, 'Noto Sans TC', sans-serif;box-sizing:border-box}.App{text-align:center}.App-main{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.sd{list-style:none;display:grid;grid-template-columns:1fr 1fr 1fr;margin:0;padding:0;width:500px;height:500px}@media screen and (max-width: 500px){.sd{width:90%;height:calc(90vw)}}.sd li{width:100%;border:1px solid white;display:grid;grid-template-columns:1fr 1fr 1fr}.sd li div{border:.5px dashed white}.sd li input{color:white;text-align:center}.loading_block{display:flex;justify-content:center;align-items:center;width:500px;height:500px}@media screen and (max-width: 500px){.loading_block{width:90%;height:calc(90vw)}}

